@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/variable/pretendardvariable.min.css");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}
button {
  background-color: transparent;
  border: none;
}
body {
  font-family: "Pretendard Variable";
  line-height: 1;
  color: #333;
  -ms-overflow-style: none;
}
::-webkit-scrollbar {
 display: none;
}
.wrap {
  width: 100%;
  max-width: 767px;
  margin: 0 auto;
  overflow: hidden;
}
header {
  padding: 20px 20px 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
/* header .hd_box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
} */
header .logo a {
  display: block;
}
header .logo a img {
  width: 40px;
  display: block;
}
header .search {
  position: relative;
  width: 100%;
  margin-left: 20px;
}
header .search input {
  width: 100%;
  height: 40px;
  border-radius: 20px;
  border: none;
  background-color: #eee;
  padding: 0 15px 0 40px;
}
header .search input::placeholder {
  font-size: 14px;
  color: #696969;
}
header .search .icon_search {
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  width: 20px;
  opacity: .6;
}
header .heart img {
  width: 32px;
  opacity: .6;
}
.main_visual {
  width: 100%;
  height: 60vw;
  max-height: 410px;
  min-height: 235px;
  margin-bottom: 50px;
}
.main_visual a {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
}
.main_visual a::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgb(0,0,0);
  background: linear-gradient(180deg, rgba(0,0,0,0) 50%, rgba(0,0,0,0.6175420168067223) 100%);
}
.main_visual img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.main_visual .text {
  position: absolute;
  bottom: 25px;
  left: 20px;
  color: #fff;
  font-size: 22px;
}
.main_visual .text strong {
  font-weight: 600;
}
.main_visual .text span {
  font-size: 14px;
  display: block;
  margin-top: 10px;
  font-weight: 500;
}
.category {
  width: calc(100% - 40px);
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 25px;
  /* display: none; */
}
.category li a figure {
  margin-bottom: 8px;
}
.category li a figure img {
  width: 15.11vw;
  height: 15.11vw;
  max-width: 65px;
  max-height: 65px;
}
.category li a span {
  display: block;
  text-align: center;
  font-size: 14px;
}
.sec03 {
  padding: 0 20px;
  margin-bottom: 50px;
}
.sec03 h2 {
  font-size: 19px;
  font-weight: 600px;
}
.sec03 .tabmenu {
  display: flex;
  justify-content: space-around;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
  padding: 0 15px;
  margin-bottom: 15px;
}
.sec03 .tabmenu span {
  font-size: 14px;
  padding: 15px 0;
  cursor: pointer;
}
.sec03 .tabmenu span.on {
  font-weight: 600;
  color: #457Ef8;
  border-bottom: 3px solid #457Ef8;
}
.sec03 .list ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  row-gap: 10px;
  display: none;
}
.sec03 .list ul.on {
  display: flex;
}
.sec03 .list ul li {
  width: calc((100% - 14px) / 2);
}
.sec03 .list ul li a {
  width: 100%;
}
.sec03 .list ul li a figure {
  width: 100%;
  height: 38vw;
  max-height: 260px;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
}
.sec03 .list ul li a figure > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.sec03 .list ul li a figure .icon_bookmark {
  position: absolute;
  top: 7px;
  right: 7px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #fff;
  cursor: pointer;
}
.sec03 .list ul li a figure .icon_bookmark img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 15px;
}
.sec03 .list ul li a .text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px 5px;
}
.sec03 .list ul li a .text h3{
  font-size: 15px;
  font-weight: 600;
  margin-bottom: 4px;
}
.sec03 .list ul li a .text span {
  font-size: 12px;
  color: #808080;
}
.sec03 .list ul li a .text span:nth-child(3) {
  text-align: end;
  margin-top: 10px;
  margin-bottom: 4px;
}
.sec03 .list ul li a .text div {
  text-align: end;
  font-size: 16px;
  font-weight: 600;
}
.sec03 .list ul li a .text div strong {
  font-size: 15px;
  font-weight: 700;
}
.sec04 {
  padding: 0 20px;
  margin-bottom: 50px;
}
.sec04 h2 {
  font-size: 19px;
  font-weight: 600;
}
.sec04 ul {
  display: flex;
  column-gap: 14px;
  overflow: scroll;
}
.sec04 ul li {
  min-width: 250px;
}
.sec04 ul li a {
  width: 100%;
}
.sec04 ul li a figure {
  position: relative;
  width: 100%;
  height: 300px;
  border-radius: 5px;
  overflow: hidden;
}
.sec04 ul li a figure::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 30%, rgba(10, 10, 10, 0.4) 70%);
}
.sec04 ul li a figure > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.sec04 ul li a .icon_bookmark {
  position: absolute;
  top: 20px;
  right: 15px;
  width: 28px;
  height: 28px;
  background-color: #fff;
  border-radius: 50%;
}
.sec04 ul li a .icon_bookmark img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 18px;
  height: 18px;
}
.sec04 ul li a .text {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 20px;
  left: 20px;
  color: #fff;
}
.sec04 ul li a .text h3 {
  font-size: 18px;
  font-weight: 600;
 
}
.sec04 ul li a .text p {
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 8px;
  margin-top: 180px;
}
.sec04 ul li a .text span {
  font-size: 14px;
  padding: 7px 14px;
  background-color: rgba(0,0,0,.5);
  border-radius: 30px;
  width: fit-content;
}
.area {
  padding: 0 20px;
  margin-bottom: 110px;
}
.area h2 {
  margin-bottom: 15px;
}
.area ul {
  display: flex;
  column-gap: 15px;
  overflow: scroll;
}
.area ul li {
  min-width: 120px;
  height: 120px;
  position: relative;
  border-radius: 50%;
  overflow: hidden;
}
.area ul li::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .3);
}
.area ul li img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.area ul li span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
}
.bottom_bar {
  max-width: 767px;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  padding: 10px 30px 20px;
  background-color: #fff;
  box-shadow: 0px -2px 8px rgba(0,0,0,.1);
  z-index: 1;
}
.bottom_bar ul {
  display: flex;
  justify-content: space-between;
}
.bottom_bar ul li a {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.bottom_bar ul li a img {
  width: 20px;
}
.bottom_bar ul li a span {
  color: #808080;
  font-size: 12px;
  margin-top: 7px;
}
.bottom_bar ul li.on a span {
  color: #2B58AF;
}
