@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/variable/pretendardvariable.min.css");
.wrap h2 {
  font-weight: 600;
  font-size: 19px;
  margin-bottom: 15px;
}
.sec01 {
  padding-left: 20px;
}
.sec02 {
  position: relative;
  padding: 0 20px 0;
}
.sec02 > a {
  position: absolute;
  top: 3px;
  right: 20px;
  font-size: 13px;
  color: #457EF8;
}
.tour_list {
  display: flex;
  column-gap: 14px;
  width: 100vw;
  overflow: scroll;
  margin-bottom: 50px;
}
.tour_list .item {
  position: relative;
  max-width: 434px;
  min-width: 268px;

}
.tour_list .item figure {
  width: 100%;
  height: 178px;
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  /* box-shadow: 2px 2px 10px rgba(0, 0, 0, .2); */
}
.tour_list .item figure::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  /* background: rgba(255, 255, 255, .2); */
  background: linear-gradient(180deg, rgba(255,255,255,0.2) 30%, rgba(10, 10, 10, 0.4) 70%);
  /* display: none; */
}
.tour_list .item figure img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.tour_list .item figure .icon_bookmark {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 25px;
  height: 25px;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, .3);
  cursor: pointer;
  z-index: 1;
}
.tour_list .item figure .icon_bookmark img {
  width: 15px;
  height: 15px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.tour_list .item .item--text {
  position: absolute;
  bottom: 12px;
  left: 12px;
  z-index: 1;
}
.tour_list .item .item--text h3 {
  color: #fff;
  margin-bottom: 8px;
  font-size: 17px;
  font-weight: 600;
}
.tour_list .item .item--text span {
  font-size: 14px;
  color: #000000;
  padding: 5px 13px;
  width: fit-content;
  border-radius: 20px;
  background-color: rgba(255,255,255, 1);
  display: flex;
  justify-content: center;
  align-items: center;
}

.food_list {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  width: 100%;
  margin-bottom: 50px;
}
.food_list .item {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;
}
.food_list .item:last-child {
  border: none;
}
.food_list .item figure {
  width: 90px;
  height: 90px;
  min-width: 90px;
  border-radius: 10px;
  overflow: hidden;
  /* box-shadow: 4px 4px 20px rgba(0, 0, 0, .3); */
  /* max-height: 322px; */
}
.food_list .item figure > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.food_list .item figure .icon_bookmark {
  position: absolute;
  top: 22px;
  right: 16px;
  cursor: pointer;
}
.food_list .item figure .icon_bookmark img {
  position: absolute;
  width: 18px;
  height: 18px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.food_list .item .item--text {
  padding: 15px;
  display: flex;
  min-width: 0; /* 말줄임표 적용하기 위해 넣음 */
  flex-direction: column;
  justify-content: center;
}
.food_list .item .item--text h3 {
  font-size: 16px;
  font-weight: 700;
}
.food_list .item .item--text .menu {
  font-size: 14px;
  color: #333;
  letter-spacing: 0px;
  font-weight: 600;
  display: flex;
  align-items: center;
  column-gap: 2px;
}
.food_list .item .item--text p {
  font-size: 15px;
  font-weight: 400;
  color: #424242;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: break-word;
  margin-top: 6px;
  margin-bottom: 12px;
}
.food_list .item .item--text .menu img {
  width: 11px;
}
.food_list .item .item--text .menu span {
  font-size: 14px;
  color: #333;
  margin-right: 1px;
  font-weight: 400;
}
